import React from "react";
import Slide from "@material-ui/core/Slide";
import { makeStyles } from "@material-ui/core/styles";
import posed from "react-pose";
import "./index.css";
const useStyles = makeStyles((theme) => ({
  sky: {
    backgroundImage: "linear-gradient(-180deg, #5fb6d9 0%, #ededdf 100%)",
    height: "70vh",
    justifyContent: "center",
    alignItems: "bottom",
    verticalAlign:"bottom",
    display: "flex"
  },
  house: {
    width: 200,
    height: 250,
    background: "#fff"
  }
}));
const Box = posed.div({
  hoverable: true,
  pressable: true,
  init: {
    scale: 1,
    boxShadow: "0px 0px 0px rgba(0,0,0,0)"
  },
  hover: {
    scale: 1.2,
    boxShadow: "0px 5px 10px rgba(0,0,0,0.2)"
  },
  press: {
    scale: 1.1,
    boxShadow: "0px 2px 5px rgba(0,0,0,0.1)"
  }
});

export default function Sky() {
  const classes = useStyles();
  const Example = () => <Box className={classes.house} />;
  return (
    <div>
      <Slide
        direction="down"
        in={true}
        timeout={{ enter: 1000, exit: 500 }}
        mountOnEnter
        unmountOnExit
      >
        <div>
          <div className={classes.sky}>
          </div>
        </div>
      </Slide>
      <Slide
        direction="down"
        in={true}
        timeout={{ enter: 1000, exit: 500 }}
        mountOnEnter
        unmountOnExit
      >
        <div>
            <Example />
        </div>
      </Slide>
    </div>
  );
}
