import React from "react";
import Slide from "@material-ui/core/Slide";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  grass: {
    backgroundImage: "linear-gradient(-180deg, #a8db8a 0%, #5c8524 100%)",
    height: "20vh"
  },
  road: {
    backgroundImage: "linear-gradient(-180deg, #7a7469 0%, #635d52 70%, #7a7469 100%)",
    height: "10vh"
  }
}));

export default function Road() {
  const classes = useStyles();
  return (
    <div>
      <Slide
        direction="up"
        in={true}
        timeout={{ enter: 1000, exit: 500 }}
        mountOnEnter
        unmountOnExit
      >
        <div>
          <div className={classes.road}></div>
          <div className={classes.grass}></div>
        </div>
      </Slide>
    </div>
  );
}
